.admin-background {
  background-image: url('../../../public/-24.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logout-button {
  position: fixed;
  top: 10px;
  right: 10px;
  background: none;
  border: 1px solid #ffffff;
  color: white;
  cursor: pointer;
  font-size: 16px;
  padding: 10px 20px;
  border-radius: 8px;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.logout-button:hover {
  background-color: #00d0ff;
  box-shadow: 0 0 10px rgba(0, 208, 255, 0.5);
}

/* Conteúdo Principal */
.content {
  display: flex;
  align-items: center;
  gap: 60px;
  padding: 20px;
  width: 100%;
  max-width: 1200px;
  margin-top: 60px; /* Espaço para o botão de logout */
}

.filters {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
  width: 100%;
  max-width: 400px;
}

.filters label {
  margin-bottom: 10px;
  font-weight: bold;
  color: #ffffff;
  align-self: flex-start;
}

.filters input,
.filters select {
  padding: 10px;
  border: 1px solid #ffffff;
  border-radius: 8px;
  background: transparent;
  color: #ffffff;
  margin-bottom: 10px;
  width: 100%;
  box-sizing: border-box;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.filters input:focus,
.filters select:focus {
  border-color: #00d0ff;
  box-shadow: 0 0 10px rgba(0, 208, 255, 0.5);
  outline: none;
}

.filters input:hover,
.filters select:hover {
  border-color: #00d0ff;
}

.filters select option {
  background-color: #333333;
  color: #ffffff;
}

.filters input[type="date"] {
background-color: white;
color: rgba(0, 0, 0, 0.7);
}

.ticket-list {
  background-color: rgba(0, 0, 0, 0.7);
  padding: 20px;
  border-radius: 10px;
  width: 100%;
  max-width: 800px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.ticket-list h2 {
  color: #ffffff;
  margin-bottom: 20px;
}

.ticket-list ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.ticket-list li {
  padding: 10px;
  border-bottom: 1px solid #ccc;
  cursor: pointer;
  color: #ffffff;
  transition: background-color 0.3s;
}

.ticket-list li:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.ticket-list li.selected {
  background-color: rgba(0, 208, 255, 0.3);
  color: #00d0ff;
}

.ticket-details {
  background-color: rgba(0, 0, 0, 0.7);
  padding: 20px;
  border-radius: 10px;
  width: 100%;
  max-width: 800px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  margin-top: 20px;
  overflow: auto; /* Adiciona rolagem se o conteúdo for grande */
}

.ticket-details h2 {
  color: #00d0ff;
  margin-bottom: 20px;
}

.ticket-details p {
  color: #ffffff;
  margin-bottom: 10px;
}

.ticket-details button {
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  color: rgb(0, 0, 0);
  background-color: #ffffff;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.ticket-details button:not(:last-child) {
  margin-right: 10px; /* Adiciona espaçamento entre os botões */
}

.ticket-details button:hover {
  background-color: #00d0ff;
  box-shadow: 0 0 10px rgba(0, 208, 255, 0.5);
}

.ticket-details button.resolve {
  background-color: #28A745;
}

.ticket-details button.resolve:hover {
  background-color: #218838;
}

.ticket-details button.delete {
  background-color: #DC3545;
}

.ticket-details button.delete:hover {
  background-color: #c82333;
}

.ticket-details button.close {
  background-color: #6C757D;
}

.ticket-details button.close:hover {
  background-color: #5a6268;
}

/* Responsividade */
@media (max-width: 599px) {
  .filters, .ticket-list, .ticket-details {
    max-width: 100%;
    padding: 10px;
  }

  .filters input, .filters select, .ticket-details button {
    width: 100%;
  }
}